import DataSource from '../../components/DataSource'
import { getStats } from '../../state/stats/source'
import StatsTable from './Table'
import { ChevronLeftIcon, ChevronRightIcon } from '@chakra-ui/icons'
import { Box, Flex } from '@chakra-ui/react'
import { useCallback, useEffect, useState } from 'react'
import ChainSelector from './components/ChainSelector'
import StatsTabs from './components/StatsTabs'

export const TABLE_TABS = [
  {
    key: 'Live minting',
    value: 'ongoing',
  },
  {
    key: 'Upcoming',
    value: 'upcoming',
  },
  {
    key: 'Free',
    value: 'free',
  },
]

const Stats = ({ accessKey }) => {
  const [tableView, setTableView] = useState(TABLE_TABS[0].value)
  const [tabIndex, setTabIndex] = useState(0)
  const [stats, setStats] = useState([])
  const [chain, setChain] = useState('')
  const [offset, setOffset] = useState(0)
  const [page, setPage] = useState(1)
  const [loading, setLoading] = useState(true)
  const [totalStats, setTotalStats] = useState(0)
  const ITEMS_PER_PAGE = 10
  const fetchStatsData = useCallback(
    async (accessKey) => {
      try {
        const { data: _stats, totalResults } = await getStats(accessKey, tableView, offset, 10, chain)
        // console.log(_stats)
        setStats(_stats)
        setTotalStats(totalResults)
      } catch (e) {
      } finally {
        setLoading(false)
      }
    },
    [chain, offset, tableView],
  )

  useEffect(() => {
    if (accessKey) {
      fetchStatsData(accessKey)
    }
  }, [accessKey, chain, tableView, fetchStatsData])

  const onClickFilter = (value) => {
    setOffset(0)
    setTotalStats(0)
    setPage(1)
    setTableView(value)
  }

  const selectChain = (_chain) => {
    setChain(() => _chain)
    setOffset(0)
    setTotalStats(0)
    setPage(1)
  }
  const handlePageChange = (newPage) => {
    setPage(newPage)
    setOffset((newPage - 1) * ITEMS_PER_PAGE)
  }

  return (
    <Box p={'0 20px'}>
      <Flex flexDir={['column-reverse', 'row', 'row']} justifyContent="space-between">
        <Box mt={[5, 0, 0]} mb={[5, 0, 0]}>
          <ChainSelector chain={chain} selectChain={selectChain} />
        </Box>
        <Flex alignItems="center">
          <StatsTabs TABLE_TABS={TABLE_TABS} active={tableView} onClick={onClickFilter} />
        </Flex>
      </Flex>
      <StatsTable data={stats} loading={loading} />

      <Flex alignItems={'center'} mt="2" justifyContent={'space-between'}>
        <Box display={['none', 'initial']} />
        <Flex ml={['4', '52', '20']}>
          <ChevronLeftIcon
            w="7"
            h="7"
            color={page === 1 ? 'gray.300' : 'gray.600'}
            cursor={page === 1 ? 'not-allowed' : 'pointer'}
            mx="1"
            onClick={page === 1 ? () => {} : () => handlePageChange(page - 1)}
          />
          <ChevronRightIcon
            w="7"
            h="7"
            color={offset + ITEMS_PER_PAGE >= totalStats ? 'gray.300' : 'gray.600'}
            cursor={offset + ITEMS_PER_PAGE >= totalStats ? 'not-allowed' : 'pointer'}
            mx="1"
            onClick={offset + ITEMS_PER_PAGE >= totalStats ? () => {} : () => handlePageChange(page + 1)}
          />
        </Flex>
        <Box mr="4">
          <DataSource />
        </Box>
      </Flex>
    </Box>
  )
}

export default Stats
