import React from 'react'
import { Button, HStack } from '@chakra-ui/react'
import styled from '@emotion/styled'

const StyledButton = styled(Button)`
  border-bottom: 2px solid ${({ theme, active }) => (active ? theme.colors.foreground : theme.colors.text)};
  color: ${({ theme, active }) => (active ? theme.colors.foreground : theme.colors.text)};
  border-radius: unset;
  margin-left: 0 !important;
  opacity: ${({ active }) => (active ? 1 : 0.4)};
  padding-bottom: 5px;
  font-size: 14px;

  &:hover {
    text-decoration: none;
  }

  &:not(:first-child) {
    padding: 0 15px 5px 15px;
  }

  &:first-child {
    padding: 0 15px 5px 15px;
  }
`

const StatsTabs = ({ TABLE_TABS, active, onClick }) => {
  return (
    <HStack>
      {TABLE_TABS.map(({ key, value }) => (
        <StyledButton key={key} color="text" variant="link" active={value === active} onClick={() => onClick(value)}>
          {key}
        </StyledButton>
      ))}
    </HStack>
  )
}

export default StatsTabs
