import React, { useState } from 'react'
import { getImageUrl } from '../../../utils'
import {
  Accordion,
  AccordionButton,
  AccordionItem,
  AccordionPanel,
  Box,
  CircularProgress,
  Container,
  Flex,
  Image,
  Progress,
  Table,
  Tbody,
  Td,
  Text,
  Th,
  Thead,
  Tr,
  useMediaQuery,
} from '@chakra-ui/react'
import TokenIcon from '../../../components/Svgs/tokenIcons'
import styled from '@emotion/styled'
import Empty from '../../../components/Svgs/empty'
import { updateSelectedCollectionId } from '../../../state/collection/reducer'
import { useDispatch } from 'react-redux'

const StyledTd = styled(Td)`
  padding: 16px 0;
  font-size: 14px;
  font-weight: 500;
`

const StyledTh = styled(Th)`
  padding-left: 0;
  text-transform: unset;
  color: ${({ theme }) => theme.colors.text};
  font-size: 14px;
`

const StatsTable = ({ loading, data }) => {
  const [isMinimum968] = useMediaQuery('(min-width: 968px)')

  const dispatch = useDispatch()

  const onClickCollection = (id) => {
    dispatch(updateSelectedCollectionId({ id }))
  }

  if (isMinimum968) {
    return (
      <Table variant="simple" borderCollapse="initial" w="full">
        <Thead>
          <Tr>
            <StyledTh color="tableColumn" textAlign="left">
              Collection
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              Launch Date
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              Total Mints
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              Mint Price
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              1h Mints
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              Mint %
            </StyledTh>
            <StyledTh color="tableColumn" textAlign="center">
              Unique Minters
            </StyledTh>
          </Tr>
        </Thead>
        <Tbody>
          {data.length > 0 ? (
            data.map((data, index) => (
              <Tr key={index}>
                <StyledTd>
                  <Flex alignItems="center" onClick={() => onClickCollection(data.collectionId)} cursor="pointer">
                    <Image
                      src={getImageUrl(data?.profile?.profileImage, { height: 100, quality: 80 })}
                      w="10"
                      h="10"
                      borderRadius={'4'}
                      alt=""
                    />
                    <Text ml="2" fontSize="sm" fontWeight={'500'} mr="6">
                      {data?.profile?.name}
                    </Text>
                  </Flex>
                </StyledTd>
                <StyledTd textAlign={'center'}>{data?.stats?.launchedOn}</StyledTd>
                <StyledTd textAlign={'center'}>{`${data?.stats?.totalSupply}/${data?.stats?.maxSupply}`}</StyledTd>
                <StyledTd>
                  <Flex alignItems="center" justifyContent={'center'}>
                    <TokenIcon width={14} height={14} chainId={data?.contract?.chainId} />
                    <Text fontSize={16} ml="1" mt={'0 !important'}>
                      {data?.stats?.price?.value ? data?.stats?.price?.value : '--'}
                    </Text>
                  </Flex>
                </StyledTd>
                <StyledTd textAlign={'center'}>{data?.stats?.hourlyMint}</StyledTd>
                <StyledTd textAlign={'center'}>{data?.stats?.mintPercentage}</StyledTd>
                <StyledTd textAlign={'center'}>
                  <Flex justifyContent={'center'} mb="1">
                    <Text>{data?.stats?.minters}%</Text>
                    <Text ml="2">{`(${data?.stats?.minters})`}</Text>
                  </Flex>
                  <Progress
                    h="2"
                    colorScheme="gray"
                    borderRadius={'8'}
                    value={
                      isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                        ? 0
                        : (data?.stats?.minters / data?.stats?.totalSupply) * 100
                    }
                  />
                </StyledTd>
              </Tr>
            ))
          ) : (
            <Tr>
              <Td opacity="0.4" colSpan={7}>
                {loading ? (
                  <Flex w="full" justifyContent={'center'} alignItems="center" p="10">
                    <CircularProgress size={'10'} color="black" isIndeterminate />
                  </Flex>
                ) : (
                  <Flex flexDir="column" justifyContent={'center'} alignItems="center" mt="8">
                    <Empty height="25px" width="25px" />
                    <Text p="2" pb="6" fontSize="xs" textTransform={'uppercase'}>
                      No Data Found
                    </Text>
                  </Flex>
                )}
              </Td>
            </Tr>
          )}
        </Tbody>
      </Table>
    )
  }
  return (
    <Table variant="simple">
      <Thead>
        <Tr display={'flex'} pb="2">
          <StyledTh w="full" color="tableColumn" border="none" textAlign={'left'}>
            Collection
          </StyledTh>
          <StyledTh w="full" color="tableColumn" border="none" textAlign={'right'}>
            Launch Date
          </StyledTh>
        </Tr>
      </Thead>
      {data.length > 0 ? (
        <Box>
          <Accordion allowToggle>
            {data.map((data, index) => (
              <Tr
                w="full"
                key={index}
                style={{
                  display: 'flex',
                }}
              >
                <AccordionItem
                  w="full"
                  // borderColor="foreground"
                  // borderStyle={'solid'}
                  // borderWidth="1.5px"
                  // borderRadius={'8'}
                  // mt="2"
                >
                  <AccordionButton w="full">
                    {/* <AccordionIcon /> */}
                    <Td w="full" border="none" p="1">
                      <h2>
                        <Flex alignItems={'center'} justifyContent="space-between">
                          <Box border="none" overflow={'hidden'} onClick={() => onClickCollection(data.collectionId)}>
                            <Flex alignItems="center">
                              <Image
                                src={getImageUrl(data?.profile?.profileImage, {
                                  height: 100,
                                  quality: 80,
                                })}
                                w="10"
                                h="10"
                                borderRadius={'4'}
                                alt=""
                              />
                              <Text
                                ml="2"
                                fontSize="sm"
                                fontWeight={'500'}
                                mr="6"
                                noOfLines={2}
                                textOverflow={'ellipsis'}
                                overflow="hidden"
                              >
                                {data?.profile?.name}
                              </Text>
                            </Flex>
                          </Box>
                          <Text textAlign={'center'} border="none">
                            {data?.stats?.launchedOn}
                          </Text>
                        </Flex>
                      </h2>
                    </Td>
                  </AccordionButton>
                  <AccordionPanel pb={4} overflow="scroll">
                    <Container w="full" overflow={'scroll'}>
                      <Flex justifyContent={'space-between'}>
                        <Box>
                          <Text color="tableColumn" textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            Total Mints
                          </Text>
                          <Text
                            textAlign={'center'}
                            fontSize="sm"
                            fontWeight={'500'}
                          >{`${data?.stats?.totalSupply}/${data?.stats?.maxSupply}`}</Text>
                        </Box>
                        <Box>
                          <Text color="tableColumn" textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            Mint Price
                          </Text>
                          <Flex justifyContent="center" alignItems="center">
                            <TokenIcon width={14} height={14} chainId={data?.contract?.chainId} />
                            <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                              {data?.stats?.price?.value}
                            </Text>
                          </Flex>
                        </Box>
                        <Box>
                          <Text color="tableColumn" textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            1h Mints
                          </Text>
                          <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            {data?.stats?.hourlyMint}
                          </Text>
                        </Box>
                        <Box>
                          <Text color="tableColumn" textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            Mint %
                          </Text>
                          <Text textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            {data?.stats?.mintPercentage}
                          </Text>
                        </Box>
                        <Box>
                          <Text color="tableColumn" textAlign={'center'} fontSize="sm" fontWeight={'500'}>
                            Unique Minters
                          </Text>
                          <Flex justifyContent={'center'} mb="1">
                            <Text>{data?.stats?.minters}%</Text>
                            <Text ml="2">{`(${data?.stats?.minters})`}</Text>
                          </Flex>
                          <Progress
                            h="2"
                            colorScheme="gray"
                            borderRadius={'8'}
                            value={
                              isNaN(data?.stats?.minters / data?.stats?.totalSupply)
                                ? 0
                                : (data?.stats?.minters / data?.stats?.totalSupply) * 100
                            }
                          />
                        </Box>
                      </Flex>
                    </Container>
                  </AccordionPanel>
                </AccordionItem>
              </Tr>
            ))}
          </Accordion>
        </Box>
      ) : (
        <Flex
          justifyContent={'center'}
          alignItems="center"
          flexDir="column"
          opacity="0.4"
          mt="4"
          w="full"
          pos="relative"
        >
          {loading ? (
            <Flex w="full" justifyContent={'center'} alignItems="center" p="10">
              <CircularProgress size={'10'} color="black" isIndeterminate />
            </Flex>
          ) : (
            <>
              <Empty height="25px" width="25px" />
              <Text p="2" pb="6" fontSize="xs" textTransform={'uppercase'}>
                No Data Found
              </Text>
            </>
          )}
        </Flex>
      )}
    </Table>
  )
}

export default StatsTable
