import TokenIcon from '../../../components/Svgs/tokenIcons'
import { SupportedChainId } from '../../../constants/chains'
import { Flex, Text } from '@chakra-ui/react'
import React from 'react'

const env = process.env.NEXT_PUBLIC_NODE_ENV
const testnetChains = [
  '',
  SupportedChainId.SOLANA_DEVNET,
  SupportedChainId.GOERLI,
  SupportedChainId.BINANCE_TESTNET,
  SupportedChainId.SHARDEUM20,
]
const mainnetChains = [
  '',
  SupportedChainId.SOLANA,
  SupportedChainId.MAINNET,
  SupportedChainId.BINANCE,
  SupportedChainId.SHARDEUM20,
]
// const chains = env === 'production' ? mainnetChains : testnetChains
const chains = mainnetChains

const ChainSelector = ({ chain, selectChain }) => {
  return (
    <Flex
      p="2"
      w="fit-content"
      alignSelf={'flex-start'}
      borderRadius={'12px'}
      borderColor="foreground"
      borderWidth={'1.5px'}
      borderStyle="solid"
      justifyContent="center"
      alignItems="center"
    >
      <Flex _last={{ borderRight: 'initial' }} cursor="pointer">
        {chains.map((_chain) => {
          if (_chain === '') {
            return (
              <Text
                key={_chain}
                fontSize={'14px'}
                fontWeight={600}
                color={chain === '' ? '#000000' : ''}
                selected={'' === chain}
                onClick={() => selectChain(_chain)}
                mr="2"
              >
                All Chains
              </Text>
            )
          }
          return (
            <Flex
              key={_chain}
              cursor="pointer"
              _hover={{
                backgroundColor: 'hover',
              }}
              style={{
                backgroundColor: _chain === chain ? 'gray.500' : 'initial',
              }}
              onClick={() => selectChain(_chain)}
              justifyContent="center"
              alignItems={'center'}
              margin="0 6px"
            >
              <TokenIcon width={18} height={18} chainId={_chain} grey={_chain === chain ? '#000000' : '#666666'} />
            </Flex>
          )
        })}
      </Flex>
    </Flex>
  )
}

export default ChainSelector
